<template>
  <div class="tw-template-content">
    <div class="login">
      <div class="login-top">
        登录
      </div>
      <div class="login-center clearfix">
        <div class="login-center-img"><img src="../assets/img/name.png" /></div>
        <div class="login-center-input">
          <input
            type="text"
            v-model="username"
            placeholder="请输入您的用户名"
            onfocus="this.placeholder=''"
            onblur="this.placeholder='请输入您的用户名'"
            :class="{ warning: nameFlag }"
            @focus="focusName"
          />
          <div class="login-center-input-text" :class="{ warning: nameFlag }">
            用户名
          </div>
        </div>
      </div>
      <div class="login-center clearfix">
        <div class="login-center-img">
          <img src="../assets/img/password.png" />
        </div>
        <div class="login-center-input">
          <input
            type="password"
            v-model="password"
            placeholder="请输入您的密码"
            onfocus="this.placeholder=''"
            onblur="this.placeholder='请输入您的密码'"
            @focus="focusPwd"
            :class="{ warning: pwdFlag }"
          />
          <div class="login-center-input-text" :class="{ warning: pwdFlag }">
            密码
          </div>
        </div>
      </div>
      <div class="login-button" @click="login">
        登陆
      </div>
    </div>
  </div>
</template>

<script>
import { ajaxT } from 'assets/js/api'

export default {
  name: 'Login',
  data() {
    return {
      username: '',
      password: '',
      pwdFlag: false,
      nameFlag: false,
    }
  },
  methods: {
    login() {
      if (!this.username) return this.$message.error('请输入用户名')
      if (!this.password) return this.$message.error('请输入密码')
      const params = new URLSearchParams()
      params.append('user_name', this.username)
      params.append('user_pwd', this.password)
      ajaxT.post('vehicleRepair/login', params).then((res) => {
        if (res.data.state == 200) {
          this.$cookies.set('userid', res.data.data[0].user_id, '30MIN')
          this.$store.state.userid = res.data.data[0].user_id
          this.$message.success('登陆成功')
          this.$router.push({ path: '/cj' })
        } else this.$message.error('登陆失败')
      })
    },
    focusPwd() {
      this.pwdFlag = false
    },
    focusName() {
      this.nameFlag = false
    },
  },
}
</script>

<style lang="scss" scoped>
.login {
  z-index: 2;
  position: absolute;
  width: 350px;
  border-radius: 5px;
  height: 500px;
  background: white;
  box-shadow: 0px 0px 5px #333333;
  top: 50%;
  left: 50%;
  margin-top: -250px;
  margin-left: -175px;
  transition: all 1s;
}
.login-top {
  font-size: 24px;
  margin-top: 100px;
  padding-left: 40px;
  box-sizing: border-box;
  color: #333333;
  margin-bottom: 50px;
}
.login-center-img {
  width: 20px;
  height: 20px;
  float: left;
  margin-top: 5px;
}
.login-center {
  width: 100%;
  box-sizing: border-box;
  padding: 0 40px;
  margin-bottom: 30px;
}
.login-center-input {
  float: left;
  width: 230px;
  margin-left: 15px;
  height: 30px;
  position: relative;
}
.login-center-img > img {
  width: 100%;
}
img {
  vertical-align: top;
  max-width: 100%;
}
.login-center-input input {
  z-index: 2;
  transition: all 0.5s;
  padding-left: 10px;
  color: #333333;
  width: 100%;
  height: 30px;
  border: 0;
  border-bottom: 1px solid #cccccc;
  border-top: 1px solid #ffffff;
  border-left: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  box-sizing: border-box;
  outline: none;
  position: relative;
  &.warning {
    border: 1px solid red;
  }
}
.login-center-input-text {
  background: white;
  padding: 0 5px;
  position: absolute;
  z-index: 0;
  opacity: 0;
  height: 20px;
  top: 50%;
  margin-top: -10px;
  font-size: 14px;
  left: 5px;
  color: dodgerblue;
  line-height: 20px;
  transition: all 0.5s;
  &.warning {
    top: 0;
    z-index: 3;
    opacity: 1;
    margin-top: -15px;
    color: red;
  }
}

.login-center-input input:focus {
  border: 1px solid dodgerblue;
}
.login-center-input input:focus ~ .login-center-input-text {
  top: 0;
  z-index: 3;
  opacity: 1;
  margin-top: -15px;
}
.login-button {
  cursor: pointer;
  width: 250px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  background-color: dodgerblue;
  border-radius: 5px;
  margin: 0 auto;
  margin-top: 50px;
  color: white;
}
</style>
